import React from 'react';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      first: "",
      second: "",
      third: ""
    };
    this.genUniqueInt = this.genUniqueInt.bind(this);
  }

  genUniqueInt(maximum, prev){
    var newnum = parseInt(Math.random()*maximum);
    while(prev.includes(newnum)){
      newnum = parseInt(Math.random()*maximum);
    }
    return newnum
  }

  componentDidMount() {
    var numbers = []

    for (var i = 0; i < 3; i++) {
      numbers.push(this.genUniqueInt(30, numbers));
    }

    this.setState({
      first: ("00" + numbers[0]).slice(-3),
      second: ("00" + numbers[1]).slice(-3),
      third: ("00" + numbers[2]).slice(-3)
    })
  }

  render(){
    return (
      <div className="App">
        <div className="info">
          <p>Some pictures are of normal strawberries. Some are of wedge strawberries.</p>
          <p>Which strawberries are the same?</p>
        </div>
        <div className="images">
          <img src={"./imgs/" + this.state.first +".jpg"} className="identify" alt="strawberry 1"/>
          <img src={"./imgs/" + this.state.second +".jpg"} className="identify" alt="strawberry 2"/>
          <img src={"./imgs/" + this.state.third +".jpg"} className="identify" alt="strawberry 3"/>
        </div>
        <div className="options">
        <form action="https://airtable-api-626.herokuapp.com/api" method="POST">
            <input type="text" id="name" name="name" placeholder="Name" required/>
            <input type="hidden" id="pic1" name="pic1" value={parseInt(this.state.first)}/>
            <input type="hidden" id="pic2" name="pic2" value={parseInt(this.state.second)}/>
            <input type="hidden" id="pic3" name="pic3" value={parseInt(this.state.third)}/>
            <select name="match" id="match">
              <option disabled value="0">None</option>
              <option value="1">1,2</option>
              <option value="2">1,3</option>
              <option value="3">2,3</option>
              <option value="4">1,2,3</option>
            </select>
            <button class="submit" type="submit">Submit</button>
          </form>
        </div>
      </div>
    );
  }
}

export default App;
